* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box
}

body {
    margin: 0 auto;
    color: #626262;
    font-family: var(--font1);
    font-size: 16px;
    line-height: 1.5;
}

body::-webkit-scrollbar {
    width: 6px;
    background-color: #fff;
}

body::-webkit-scrollbar-thumb {
    background-color: #222;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #fff;
}

.container {
    position: relative;
}

ul,
li {
    list-style-type: none;
}

.icon-svg {
    width: 20px;
    height: 20px;
    fill: currentColor;
    vertical-align: middle;
}

img {
    border: 0px;
    font-size: 0;
    line-height: 0;
    max-width: 100%;
    vertical-align: middle;
}

a {
    color: #00234B;
    text-decoration: none;
    &:hover {
        text-decoration: none;
        color: var(--colormain)
    }
}

.hidden {
    display: none !important
}

.clearfix {
    clear: both;
    &::after {
        content: "";
        display: table;
    }
}

u {
    font-weight: 400;
}

.uppercase {
    text-transform: uppercase;
}

